#meo {
  color: rgb(0, 179, 250);
}

body {
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  flex-direction: column;
  -webkit-box-flex: 0;
  flex-grow: 0;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  margin-top: 10px;
}

#submit {
  background: #109f67;
  width: 100%;
  height: 50px;
  font: 20px Helvetica;
  text-transform: uppercase;
  margin: 0 auto 40px;
  color: #fff;
  border-radius: 6px;
}


form label {
  position: relative;
  display: block;
}

form label input {
  font: 18px Helvetica, Arial, sans-serif;
  box-sizing: border-box;
  display: block;
  border: none;
  padding: 20px;
  width: 300px;
  margin-bottom: 20px;
  font-size: 18px;
  outline: none;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

form label input::-webkit-input-placeholder {
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  color: #999;
  font: 18px Helvetica, Arial, sans-serif;
}

form label input::-moz-placeholder {
  -moz-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  color: #999;
  font: 18px Helvetica, Arial, sans-serif;
}

form label input:-ms-input-placeholder {
  -ms-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  color: #999;
  font: 18px Helvetica, Arial, sans-serif;
}

form label input::-ms-input-placeholder {
  -ms-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  color: #999;
  font: 18px Helvetica, Arial, sans-serif;
}

form label input::placeholder {
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  color: #999;
  font: 18px Helvetica, Arial, sans-serif;
}

form label input:focus,
form label input.populated {
  padding-top: 28px;
  padding-bottom: 12px;
}

form label input:focus::-webkit-input-placeholder,
form label input.populated::-webkit-input-placeholder {
  color: transparent;
}

form label input:focus::-moz-placeholder,
form label input.populated::-moz-placeholder {
  color: transparent;
}

form label input:focus:-ms-input-placeholder,
form label input.populated:-ms-input-placeholder {
  color: transparent;
}

form label input:focus::-ms-input-placeholder,
form label input.populated::-ms-input-placeholder {
  color: transparent;
}

form label input:focus::placeholder,
form label input.populated::placeholder {
  color: transparent;
}

form label input:focus+span,
form label input.populated+span {
  opacity: 1;
  top: 10px;
}

form label span {
  color: #35dc9b;
  font: 13px Helvetica, Arial, sans-serif;
  position: absolute;
  top: 0px;
  left: 20px;
  opacity: 0;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

form input[type="submit"] {
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  font: 18px Helvetica, Arial, sans-serif;
  border: none;
  background: #1aaf75;
  color: #fff;
  padding: 16px 40px;
}

form input[type="submit"]:hover {
  background: #109f67;
}